@import "../variables";
@import "../mixins";


.research {
  display: none;


  @media (min-width: $tablet) {
    display: block;
    margin-bottom: 50px;
  }

  @media (min-width: $desktop) {
    background-color: #f2f2f2;
    margin-bottom: 32px;
    padding-bottom: 38px;
  }
}

.research__wrapper {
  position: relative;

  @media (min-width: $tablet) {
    padding: 67px 0 64px 328px;

    margin-left: 0;
    background-image: url("../img/icon-timer-fill.svg"), url("../img/icon-timer.svg"), url("../img/icon-24.svg");
    background-size: 205px 205px, 238px 276px, 106px 70px;
    background-position: 8% 64%, 5% 51%, 15.5% 56%;
    background-repeat: no-repeat;
  }

  @media (min-width: $desktop) {
    padding: 110px 0 70px 377px;
    background: url("../img/icon-timer-fill.svg"), url("../img/icon-timer.svg"), url("../img/icon-24.svg");
    background-size: auto, auto, 133px 100px;
    background-position: 5% 61%, 2% 34%, 11.5% 54%;
    background-repeat: no-repeat;
    margin-left: 0;
  }
}

