.research__heading {
  margin: 0;
  font-family: "OpenSansBold", "Arial", sans-serif;
  font-size: 30px;
  font-weight: 600;

  @media (min-width: $tablet) {
    width: 170px;
    line-height: 36px;
  }

  @media (min-width: $desktop) {
    width: 300px;
    margin-bottom: 26px;
  }
}

.research__page-text {
  margin: 0;
  font-family: "OpenSans", "Arial", sans-serif;
  font-size: 14px;

  @media (min-width: $tablet) {
    width: 300px;
    font-size: 14px;
    line-height: 24px;
    margin-top: 10px;
    margin-bottom: 12px;
  }

  @media (min-width: $desktop) {
    font-size: 18px;
    width: 495px;
    margin-bottom: 45px;
    padding-top: 6px;
  }
}

.research__link {
  text-decoration: underline;
  color: #d22856;
  font-family: "OpenSans", "Arial", sans-serif;
  font-size: 14px;

  @media (min-width: $desktop) {
    font-size: 18px;
    line-height: 30px;
  }
}

