@import "../variables";
@import "../mixins";

.location {
  position: relative;

  @media (min-width: $tablet) {
    margin-bottom: 30px;
  }

  @media (min-width: $desktop) {
    margin-bottom: 24px;
  }
}

.location__wrapper {
  position: relative;
  width: 100%;
  height: 240px;

  @media (min-width: $tablet) {
    height: 375px;
  }

  @media (min-width: $desktop) {
    height: 525px;
  }
}

.location__wrapper::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  background-image: url("../img/bg-triangle-white-footer.svg");
  background-size: 100%;
  background-repeat: no-repeat;
  background-position: 0% 0%;
  top: 0%;
  left: 0%;
}
